<template>
  <div>
    <van-search v-model="search" placeholder="请输入搜索关键词" />

    <van-tree-select height="90vh"  @click-nav="onChange" :items="items" :main-active-index.sync="active" >
      <template #content>
        <van-row gutter="20" class="main_list">
          <van-col span="8" v-for="(item,index) in list" :key="index">
            <img :src="item.img" width="100%">
            <span class="goods_title">
              {{item.title}}
            </span>
          </van-col>
        </van-row>
      </template>
    </van-tree-select>


  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        active: 0,
        items: [{
            id: 1,
            text: '手机数码'
          }, {
            id: 2,
            text: '京东超市'
          },
          {
            id: 3,
            text: '京东超市'
          }
        ],

        list: [{
            id: '1',
            img: 'https://img11.360buyimg.com/focus/s140x140_jfs/t1/117080/5/10502/18158/5ef0103eE416ae569/898a7ac882ba6c63.jpg',
            title: '空调',
          },
          {
            id: '2',
            img: 'https://img10.360buyimg.com/focus/s140x140_jfs/t1/119891/13/5448/24096/5ef01048E096cfd33/260803efca5e8a73.jpg',
            title: '冰箱',
          },
          {
            id: '3',
            img: 'https://img12.360buyimg.com/focus/s140x140_jfs/t1/131424/31/274/19042/5ecb3bc2E96ef8448/0188085e6fd6cd12.png',
            title: '电脑',
          }
        ]

      };
    },
    methods: {
      onChange(index){
        if(this.active==1){
         // this.list=[];
        }
        console.log(this.active);
      }
    }
  }
</script>

<style>
  .main_list {
    width: 96%;
    padding: 2%;
  }

  .goods_title {
    width: 100%;
    text-align: center;
    height: 40px;
    line-height: 40px;
    overflow: hidden;
    display: inherit;
  }
</style>



